$('#checked-click').click(function(){
		console.log($('.auto-login-checkbox').is(':checked'));

	if ($('.auto-login-checkbox').is(':checked')) {
		console.log('done');
		$('.faked-checkbox').css({
			background: 'url(images/checked.jpg)'
			
		})
	}
	else{
		$('.faked-checkbox').css({
			background: 'url(images/unchecked.jpg)'
			
		})
	}

});